@import url(https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,200;0,300;0,400;0,600;0,800;1,200&display=swap);
:root {
  --font-base: 'Cormorant Upright', serif;
  --font-alt: 'Open Sans', sans-serif;
  
  --color-golden: #DCCA87;
  --color-black: #3c3f45;
  --color-gray: #545454;
  --color-crimson: #F5EFDB;
  --color-grey: #AAAAAA;
  --color-white: #FFFFFF;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
  overflow-x: hidden;
}

body {
  font-family: "Nunito", sans-serif;
}

section,
footer,
.lower__footer {
  margin: 0 5vw;
}

section:first-of-type {
  margin: 0;
}

section {
  margin-top: 5rem;
  margin-bottom: 5rem;
}

::-webkit-scrollbar {
  background-color: white;
  width: 0.4vw;
}

::-webkit-scrollbar-thumb {
  background: linear-gradient(to bottom, #fc4958, #e85d04, #fc4958);
}

.show {
  opacity: 1 !important;
  visibility: visible !important;
  right: 0 !important;
}

.flex__center {
    display: flex;
    justify-content: center;
    align-items: center;
}

.app__gallery {
    flex-direction: row;
    background: var(--color-black);
    padding: 2rem 0 2rem 0rem;
    grid-gap: 3rem;
    gap: 3rem
}

.app__gallery-content {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    padding-right: 2rem;
}

.app__gallery-content button {
    margin-top: 1rem;
}

.app__gallery-images {
    display: flex;
    flex-direction: row;
    max-width: 70%;
    position: relative;
}

.app__gallery-images_container {
    display: flex;
    flex-direction: row;
    width: -webkit-max-content;
    width: max-content;
    overflow-x: scroll;
    -ms-overflow-style: none;
    scrollbar-width: none;  
}

.app__gallery-images_container::-webkit-scrollbar {
    display: none;
}

.app__gallery-images_card {
    position: relative;
    min-width: 290px;
    height: 310px;
    margin-right: 1.5rem;
}

.gallery__image-icon {
    position: absolute;
    color: #fff;
    font-size: 2rem;
    opacity: 0;
    transition: .5s ease;
    cursor: pointer;
}

.app__gallery-images_card img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    opacity: 1;
    border-radius: 15px;
    transition: .5s ease;
}

.app__gallery-images_card:hover img {
    opacity: .35;
}

.app__gallery-images_card:hover .gallery__image-icon {
    opacity: 1;
}

.app__gallery-images_arrows {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 0 1rem;
    position: absolute;
    bottom: 5%;
}

.gallery__arrow-icon {
    color: var(--color-golden);
    font-size: 2rem;
    cursor: pointer;
    background: var(--color-black);
    border-radius: 5px;
}

.gallery__arrow-icon:hover {
    color: var(--color-white);
}

@media screen and (min-width: 2000px) {
    .app__gallery-content button {
        margin-top: 2rem;
    }

    .app__gallery-content {
        min-width: 1000px;
        padding-right: 4rem;
    }

    .app__gallery-images_card {
        min-width: 400px;
        height: 547px;
    }
}

@media screen and (max-width: 1150px) {
    .app__gallery {
        flex-direction: column;
    }

    .app__gallery-images {
        max-width: 100%;
        margin: 5rem 0;
    }
}

@media screen and (max-width: 850px) {
    .app__gallery {
        padding: 4rem 0 4rem 4rem;
    }
}

@media screen and (max-width: 650px) {
    .app__gallery {
        padding: 4rem 0 4rem 2rem;
    }  
    
    .app__gallery-content {
        min-width: 100%;
    }

    .app__gallery-images_card {
        min-width: 240px;
        height: 320px;
    }
}




.app__header-h1 {
    font-family: inherit;
    color: var(--color-golden);
    font-weight: 700;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    line-height: 117px;
    font-size: 90px;
}

.app__header-img img {
    width: 80%;
}

@media screen and (min-width: 2000px) {
    .app__header-h1 {
        font-size: 180px;
        line-height: 210px;
    }
}

@media screen and (max-width: 1280px) {
    .app__header-h1 {
        font-size: 60px;
    line-height: 70px;
    width: 100%;
    }
}

@media screen and (max-width: 650px) {
    .app__header-h1 {
        font-size: 40px;
        line-height: 50px;
        width: 100%
    }
}

@media screen and (max-width: 850px){
.section__padding {
    padding: 3rem 2rem;
}
}
@media screen and (max-width: 1150px){
.app__wrapper {
    flex-direction: column;
    
}}
.app__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-height: 100vh;
    grid-gap: 20%;
    gap: 20%;
}
.section__padding {
    padding: 1rem 2rem 2rem 3rem;
}
@media screen and (max-width: 1150px){
.app__wrapper_img img {
    width: 100%;
}
}
@media screen and (max-width: 1150px){
.app__wrapper_img {
    margin: 4rem 0 0;
}}
.app__wrapper_img img {
    width: 90%;
}
.custom__button {
    background: linear-gradient(to right, #e85d04, #fc4958);
    border: none;
    border-radius: 24px;
    color: black;
    cursor: pointer;
    font-family: revert;
    font-size: 16px;
    font-weight: 700;
    letter-spacing: .04em;
    line-height: 28px;
    outline: none;
    padding: 0.5rem 1.5rem;
}
.app__wrapper_img {
    align-items: center;
    flex: 1 1;
    justify-content: center;
}
.app__wrapper_info {
    align-items: flex-start;
    flex: 1 1;
    flex-direction: column;
}

